import React from 'react'
import Link from 'gatsby-link'

const Navigation = () => (
  <div className='fixed bg-white shadow w-20 h-100 ph4 dib-ns dn' style={{zIndex: '99999'}}>
   <h1 className="f2 fw5 fw2 mb0 lh-title bottom-2 bb pb3 divider" 
         style={{ bottom: '124px' }}>Vivy <span className='fw4'>Design</span></h1>
        <ul className="ma0 pl0 measure w- mt3">
          <a href='#0' className="db dim primary fw5 link lh-copy pv3 link-active">Logo</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Colors</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Typography</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Icongrapghy</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Illustrations</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Buttons and links</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Inputs</a>
          <a href='#0' className="db dim primary fw5 link lh-copy pv3">Navigation</a>
        </ul>
        </div>
)

export default Navigation



