import React from 'react'
import Link from 'gatsby-link'

const Typography = () => (
        <main className="ph3 ph5-ns mt6">
        <h1 className='primary fw4'>Typography</h1>
        <p className="f5 lh-copy measure primary">
        We use TT Norms, giving text in Vivy a fun and friendly feel to match our illustrations.</p>
  


        <div className='w-50-ns w-100 dib'>
        <h1 className='fw4 measure primary'>Headline</h1>
        <h1 className='fw4 measure primary'>Lorem ipsum dolor sit amet, nec dicta commodo.</h1>
        <h2 className='fw4 measure primary mt5'>Title</h2>
        <h2 className='fw4 measure primary'>Lorem ipsum dolor sit amet, nec dicta commodo.</h2>
        <h3 className='fw4 measure primary mt5'>Subheading 2</h3>
        <h3 className='fw4 measure primary'>Lorem ipsum dolor sit amet, nec dicta commodo.</h3>
        <h3 className='fw3 measure primary mt5'>Subheading</h3>
        <h3 className='fw3 measure primary'>Lorem ipsum dolor sit amet, nec dicta commodo.</h3>
        </div>




        <div className='w-50-ns w-100 dib v-top'>
        <p className='fw5 measure primary'>Body 2</p>
        <p className='fw5 measure primary'>Lorem ipsum dolor sit amet, nec dicta commodo.</p>
        <p className='fw4 measure primary mt5'>Body</p>
        <p className='fw4 measure primary'>Lorem ipsum dolor sit amet, nec dicta commodo.</p>
        <p className='fw4 f6 measure secondary mt5'>Caption</p>
        <p className='fw4 f6  measure secondary'>Lorem ipsum dolor sit amet, nec dicta commodo.</p>
        <h3 className='fw5 ttu measure primary mt5'>Button</h3>
        <h3 className='fw5 ttu measure primary'>Action text</h3>
        </div>






      </main>



)

export default Typography



